.app-container{
  display: flex;
  flex-direction: column;
  /* min-height: calc(100vh-130px); */
}

/* toggle */
.theme-toggle {
  display: flex;
  align-items: center;
  margin-left: auto;
  border-radius: 2px;
}

.react-switch .theme-icon {
  position: absolute;
  left: 8px;
  top: 5px;
  width: 24px;
  height: 24px;
  border-color: 'black';
  border-radius: 2px;
}


.content-container {
  flex: 1;
  padding-top: 80px; /* Ensure content does not go under the fixed header */
  padding-bottom: 50px; /* Ensure content does not go under the fixed footer */
  overflow-y: auto;
}

