.footer-fixed {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 0;
}

/* src/App.css */
.content-container::-webkit-scrollbar {
  width: 12px;
}

.content-container::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.content-container::-webkit-scrollbar-thumb {
  background-color: rosybrown;
  border-radius: 10px;
  border: 3px solid #f1f1f1;
}


/* src/App.css */
body.light-theme {
  background-color: #f4f5f6;
  /* color: #000000; */
}

body.dark-theme {
  background-color: #102233;
  /* color: #ffffff; */
}

.navbar-fixed .navbar-light {
  background-color: #c9d2dc !important;
}

.navbar-fixed .navbar-dark {
  background-color: #1c2037 !important;
}

.nav-link-light {
  color: rosybrown !important;
}

.nav-link-dark {
  color: rosybrown !important;
}

.theme-toggle-button {
  background: none;
  border: none;
  cursor: pointer;
}

.theme-icon {
  width: 24px;
  height: 24px;
}
